import Box from "@material-ui/core/Box";
import React from "react";
import {RootState} from "./reducers";
import {connect, ConnectedProps} from "react-redux";

class NaglowekInternal extends React.Component<PropsFromRedux> {
    render() {
        const { dane_gminy } = this.props
        const { gmina, typ_gminy, ludnosc = 1 } = dane_gminy;
        let mieszkancy_string: string;
        if (ludnosc >=100000) {
            mieszkancy_string = "" + (Math.round(ludnosc / 100) / 10).toLocaleString('pl') + " tys.";
        } else {
            mieszkancy_string = "" + ludnosc;
        }
        return gmina?<React.Fragment>
            <Box className="wstep">
                <Box className="prawa">
                    <Box className="nazwa_gminy"><strong>{gmina}</strong></Box>
                    {/*<Box>{typ_gminy?.replace(gmina || 'ble', '')}</Box>*/}
                    <Box><strong>{mieszkancy_string}</strong>&nbsp;{(gmina !== '')?"mieszkańców":""}</Box>
                </Box>
            </Box>
        </React.Fragment>:null
    }
}

const mapStateToProps = (state: RootState) => {
    return {
        dane_gminy: state.dane_gminy,
    };
};

const connector = connect(
    mapStateToProps
);

type PropsFromRedux = ConnectedProps<typeof connector>

export default connector(NaglowekInternal);